import React, { useState } from "react";
import { Avatar } from "@material-ui/core";
import VideocamIcon from "@material-ui/icons/Videocam";
import PhotoLibraryIcon from "@material-ui/icons/PhotoLibrary";
import InsertEmoticonIcon from "@material-ui/icons/InsertEmoticon";
import "./CreatePost.css";
import ShowPost from "./ShowPost";
import "./ShowPost.css";

function CreatePost() {
  const [input, setInput] = useState("");
  const [imageUrl, setImageUrl] = useState("");
  const [addItem, setAddItem] = useState([]);
  // const [addName, setAddName] = useState([]);

  const post1 =
    "https://images.unsplash.com/photo-1544256718-3bcf237f3974?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1502&q=80";
  // const post2 =
  //   "https://images.unsplash.com/photo-1523800503107-5bc3ba2a6f81?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1000&q=80";
  // const post3 =
  //   "https://images.unsplash.com/photo-1513061780970-4f34b3f350dd?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2089&q=80";

  const profile1 =
    "https://images.unsplash.com/photo-1518806118471-f28b20a1d79d?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2000&q=80";

  const profile2 =
    "https://images.unsplash.com/photo-1534308143481-c55f00be8bd7?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2088&q=80";

  // const profile3 =
  //   "https://images.unsplash.com/photo-1492562080023-ab3db95bfbce?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1462&q=80";

  const handleSubmit = (e) => {
    e.preventDefault();
    if (input !== "") {
      setAddItem((old) => {
        return [...old, input];
      });
    }
    // if (imageUrl !== "") {
    //   setAddName((olds) => {
    //     return [...olds, imageUrl];
    //   });
    // }

    setInput("");
    setImageUrl("");
  };

  return (
    <>
      <div className="messageSender">
        <div className="messageSender_top">
          <Avatar src={profile1} />
          <form onSubmit={handleSubmit}>
            <input
              value={input}
              onChange={(e) => setInput(e.target.value)}
              type="text"
              placeholder={`What's on your mind?`}
              className="messageSender_input"
            />
            <input
              value={imageUrl}
              onChange={(e) => setImageUrl(e.target.value)}
              placeholder="Name"
            />
            <button type="submit">Hidden submit</button>
          </form>
        </div>

        <div className="messageSender_bottom">
          <div className="messageSender_option">
            <VideocamIcon style={{ color: "red" }} />
            <h3>Live Video</h3>
          </div>

          <div className="messageSender_option">
            <PhotoLibraryIcon style={{ color: "green" }} />
            <h3>Photo/Video</h3>
          </div>

          <div className="messageSender_option">
            <InsertEmoticonIcon style={{ color: "orange" }} />
            <h3>Feeling/Activity</h3>
          </div>
        </div>
      </div>

      <div>
        {addItem.map((items, index) => {
          return (
            <div key={index}>
              <ShowPost
                post={post1}
                profile={profile2}
                name="Mudassir"
                time="12h"
                text={items}
              />
            </div>
          );
        })}
      </div>
    </>
  );
}

export default CreatePost;
